@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  font-family: "Nunito", sans-serif;
  background-color: rgb(235, 218, 201);
}
body {
  padding: 0;
  margin: 0;
}
.header {
    display: flex;
    align-items: center;
    flex-direction: column;
    background:rgb(46, 8, 8);
    color: white;
    padding: 20px 0; 
    width: 100%;
}

.font-link {
    font-family: 'Lobster', cursive;
  }
.footer {
display: flex;
flex-direction: column;
align-items: center;
background-color: rgb(48, 3, 3);
color:  rgb(243, 238, 233);
width: 100%;
}

.card{
    width: 20rem;
}

.card:hover{
    box-shadow: 5px 10px 20px 1px rgb(41, 0, 0)!important;
}

.card-body{
    padding: 3rem 0 !important;
    background-color: white;
}


.container-fluid.row{
    padding-top: 30rem;
}

.card-img-top{
    transform: scale(1.002);
    transition: transform 0.5s ease;
}

.card-img-top:hover{
    transform: scale(1.1);
}
.searchInputs {
  margin-top: 70px;
  display: flex;
}

.search input {
  background-color: white;
  border: 0;
  border-radius: 2px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  font-size: 18px;
  padding: 25px;
  height: 30px;
  width: 500px;
  color: black;
}

.searchIcon {
  height: 50px;
  width: 50px;
  background-color: rgb(48, 3, 3);
  display: grid;
  place-items: center;
}

input:focus {
  outline: none;
}
.searchIcon svg {
  font-size: 25px;
}

.dataResult {
  position: absolute;
  margin-top: 3px;
  width: 500px;
  height: 60px;
  background-color: rgb(48, 3, 3);
  box-shadow: rgb(41, 0, 0) 0px 5px 15px;
  overflow: hidden;
  overflow-y: auto;
}

.dataResult .dataItem {
  width: 300%;
  height: 30px;
  display: flex;
  align-items: center;
  color: white;
}

.dataItem p {
  margin-left: 10px;
  margin-bottom: 5px;
}

a:hover {
  background-color: lightgrey;
}

#clear {
  cursor: pointer;
}

